.sign_in,
.sign_up_agreement {
  width: 768px;
  height: 100vh;
  margin: auto;
}

.sign_logo {
  width: 350px;
  height: 100px;
  background-image: url(../img/logo/logo_big.png);
  background-size: cover;
  margin: 75px auto;
  margin-bottom: 50px;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="submit"] {
  display: block;
  width: 350px;
  height: 45px;
  border-radius: 13px;
  margin: auto;
  border: 1px solid #a4a4a4;
  padding: 0 20px;
  box-sizing: border-box;
  font-size: 0.9rem;
  cursor: pointer;
}

input[type="text"],
input[type="password"] {
  padding-top: 2.5px;
}

input[type="submit"] {
  border: 0;
  background-color: #f27e65;
  color: white;
  font-size: 1.2rem;
  height: 60px;
}

input:focus {
  outline: 0;
}

.hidden {
  display: none;
}

/* SignIn */

.sign_in input:last-child {
  margin-top: 60px;
}

.sign_in input[type="text"]:focus,
input[type="password"]:focus {
  outline: 0;
  border: 1px solid #f27e65;
}

.sign_in_link {
  width: 350px;
  margin: auto;
  margin-top: 15px;
  text-align: center;
  font-size: 0.8rem;
}

.sign_in_link span {
  color: #a4a4a4;
  margin: 0 5px;
}

/* SignUpAgreement */
.agreement_text {
  width: 350px;
  margin: auto;
  font-size: 0.95rem;
}

.agreement_text h3 {
  font-size: 1.2rem;
}

.agreement_text input[type="checkbox"] {
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 1px solid #a4a4a4;
  box-sizing: border-box;
  position: relative;
}

.agreement_text input[type="checkbox"]:checked {
  background-color: #f27e65;
  border: 0;
}

.agreement_text input[type="checkbox"]:checked:after {
  content: url(../img/icon/Vector.png);
  background-position: center;
  padding-top: 3px;
  width: 12px;
  height: 12px;
  padding: 2px 1.5px;
  position: absolute;
  top: 1px;
}

.agreement_allagree {
  display: flex;
  align-items: center;
  margin: 50px 0;
}

.agreement_allagree label {
  padding-left: 8px;
  padding-top: 6px;
}

.agreement_check li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.agreement_check label {
  box-sizing: border-box;
  width: 290px;
  padding: 0 5px;
  padding-top: 6px;
}

.agreement_check span {
  border: 0;
  background-color: parent;
  color: #a4a4a4;
  font-size: 0.7rem;
  padding: 1px 0;
  cursor: pointer;
}

.agreement_text input[type="submit"] {
  margin-top: 50px;
}

/* SignUpForm */
.sign_up_form {
  width: 350px;
  margin: auto;
}

.sign_up_form .sign_logo {
  margin-top: 30px;
  margin-bottom: 70px;
}

.user p {
  margin-top: 10px;
  margin-bottom: 5px;
}

.sign_up_form input {
  margin-top: 3px;
  margin-bottom: 0;
}

.user .confirmbtn,
.user_address #confirmbtn,
.user_phone .confirmbtn {
  display: inline-block;
  margin: 0;
  margin-left: 5px;
  box-sizing: border-box;
  width: 95px;
  height: 45px;
  background-color: #f27e65;
  text-align: center;
  border-radius: 13px;
  padding: 16.5px 0;
  color: white;
  font-size: 0.8rem;
  cursor: pointer;
}

.user_address #confirmbtn {
  padding: 0;
  border: 0;
}

.user input[type="password"] {
  width: 300px;
  display: inline-block;
  border: 0;
  margin: 0;
}

.user span,
.user_info span {
  display: block;
  font-size: 0.65rem;
  color: gray;
  margin-top: 5px;
  padding: 0 5px;
}

.user_id ul,
.user_address ul,
.user_phone ul {
  display: flex;
  align-items: center;
}

.user_id ul input,
.user_address #sample3_postcode,
.user_phone ul input {
  width: 250px;
  margin: 0;
  display: inline-block;
}

.user_address ul + input,
.user_phone ul + input {
  margin-top: 5px;
}

.confirmVerificationCode {
  margin-top: 3px;
}

.user_pw p {
  margin-top: 20px;
}

.user_pw .padlock {
  display: inline-block;
  background-image: url(../img/icon/padlock.png);
  background-position: center;
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  padding: 0 10px;
}

.user_pw .padlock_on {
  display: inline-block;
  background-image: url(../img/icon/padlock_on.png);
  background-position: center;
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  padding: 0 10px;
}

.user_pw .pw {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  border: 1px solid #a4a4a4;
  border-radius: 13px;
}

.user_info {
  margin-top: 20px;
}
.user_info p {
  margin-top: 20px;
  margin-bottom: 5px;
}

.user_info select {
  appearance: none;
  -webkit-appearance: none;
  background: url(../img/icon/arrow_dropdown_off.png);
  background-repeat: no-repeat;
  background-size: 13px;
  background-position: right 20px center;
  width: 105px;
  text-align: left;
  height: 45px;
  border-radius: 13px;
  border: 1px solid #a4a4a4;
  color: gray;
  padding: 0 20px;
  cursor: pointer;
  outline: 0;
}


.user_info select.on {
  outline: 0;
  appearance: none;
  -webkit-appearance: none;
  background: url(../img/icon/arrow_dropdown_on.png);
  background-repeat: no-repeat;
  background-size: 13px;
  background-position: right 20px center;
}

.user_info .optionbox {
  border-radius: 13px;
}

.user_birth input {
  width: 130px;
  display: inline-block;
}

.user_birth input + select {
  margin: 0 5px;
}

.user_gender .gender {
  width: 350px;
}


.sign_up_form form input[type="submit"] {
  margin-top: 70px;
  margin-bottom: 100px;
}
